body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f5f5; /* Light background color */
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  color: #333;
}

/* Adjusted button styles */
.button {
  background-color:#500075; /* Blue background */
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 20px; /* Space above button */
}

.button:hover {
  background-color: #8c0cf5e7; /* Darker blue on hover */
}

/* Container for button and token list */
.container {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  align-items: flex-end; /* Align items to the end */
}

/* Token list styles */
.token-list {
  margin-top: 20px;
  background-color: white; /* White background for the token list */
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 15px;
}

/* Token item styles */
.token-item {
  border-bottom: 1px solid #ddd; /* Divider between tokens */
  padding: 10px 0;
}

.token-item:last-child {
  border-bottom: none; /* Remove divider for the last item */
}

/* Token details */
.token-item span {
  display: block; /* Display token details in block format */
  color: #555; /* Darker text color for token details */
  max-width: 100%; /* Prevent overflow */
  overflow: hidden; /* Hide overflow */
  white-space: nowrap; /* Prevent text wrapping */
  text-overflow: ellipsis; /* Add ellipsis for overflow text */
}
